/* ******************************************************************
	Holy Grail Layout for browsers that do NOT support CSS Grid Layout
********************************************************************** */
.container > * {
  color: #fff;
  text-shadow: 0 2px 0 rgba(110, 133, 156, 0.12);
  padding: 0.85em;
  border: solid 1px rgba(110, 133, 156, 0.15); }

header {
  background-color: #3f8abf; }

nav {
  background-color: #fbaea8; }

main {
  background-color: #aad2ed; }

aside {
  background-color: #6ad78a; }

footer {
  background-color: #6e859c; }

.container {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto; }

main {
  flex: 1 0 auto; }

@media (max-width: 700px) {
  .container {
    display: flex;
    flex-direction: column; }
    .container nav, .container aside {
      width: 100%; } }

/* *************************************************************
	Holy Grail Layout for browsers that DO support CSS Grid Layout
**************************************************************** */
@supports (display: grid) {
  /* =====================================
     Grid Layout
  ======================================== */
  .container {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-rows: minmax(150px, auto);
    grid-gap: 10px;
    grid-template-areas: "header" "nav" "main" "aside" "footer"; }
  header {
    grid-area: header; }
  nav {
    grid-area: nav; }
  main {
    grid-area: main; }
  aside {
    grid-area: aside; }
  footer {
    grid-area: footer; }
  @media (min-width: 768px) {
    .container {
      grid-template-columns: 1fr 2fr 1fr;
      grid-template-rows: 100px minmax(150px, auto) 100px;
      grid-template-areas: "header header header" "nav main aside" "footer footer footer"; } }
  @media (min-width: 992px) {
    .container {
      grid-template-areas: "header header header" "nav main aside" "nav footer footer"; } } }

